import React, { useState, useEffect, useRef } from 'react'
import { getGraphData } from './getGraphDataFunc';

export default function ({ graph, current_step }) {

    // container11 DOM
    const containerRef = useRef(null);


    useEffect(() => {

        if (!containerRef.current) return;

        // container11
        const rect = containerRef.current.getBoundingClientRect();

        let grData = getGraphData(current_step, rect.width, rect.height);

        try {
            graph && graph.data(grData);
            graph && graph.render();
        } catch (error) {
            console.error(error)
        }

        // graph
        if (graph) {
            graph.changeSize(rect.width, rect.height);
        }

    }, [graph, current_step])

    return (
        <div ref={containerRef} id="container21" className="x6-graph" />
    )
}
